import React from 'react'
import ChatPanel from './ChatPanel'
import Header from './Header'
function ChatPanels() {
    const image='data:image/jpeg;base64,'
    return (
        <div>
            <Header/>
            <h1 className='chatpanel_title'>Messages</h1>
        <div className='chats'>
            <ChatPanel name='kanna' message='hello'
             image={image} timestamp='10 minutes ago'/>

            <ChatPanel name='kanna' message='hello'
             image={image} timestamp='10 minutes ago'/>

            <ChatPanel name='kanna' message='hello'
             image={image} timestamp='10 minutes ago'/>
        </div>
        </div>
    )
}

export default ChatPanels